.App{text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Open Sans", sans-serif}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:50vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.login{width:27em;height:40em;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:17px 10px}.login .right-side{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;height:80%;background-color:#3498db;width:100%;position:absolute;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;-webkit-align-items:flex-end;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;-webkit-align-items:flex-start;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:20px;padding:5px 20px;border:0;background-color:#3498db;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2386c8}.btn:focus{outline:none}

.base-container{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.base-container .header{font-size:24px;font-family:"Open Sans", sans-serif}.base-container .form{margin-top:2em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;height:80%}.base-container .form .form-group{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .form .form-group label{font-size:20px}.base-container .form .form-group input{margin-top:10px;margin-bottom:20px;min-width:18em;height:35px;padding:0px 10px;font-size:16px;font-family:"Open Sans", sans-serif;background-color:#f3f3f3;border:0;border-radius:4px;transition:all 250ms ease-in-out}.base-container .form .form-group input:hover{background-color:#ffffff;box-shadow:0px 0px 14px 0.3px #0e81ce96}.base-container .form .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #3474dbb2}.base-container .footer{margin-top:3em}

.h1,.h2,.h3{font-family:'Courier New', Courier, monospace}.base{width:100%}.base .top-container{display:-webkit-flex;display:flex;height:150px;width:100%;background-color:#3498db;margin-bottom:-20px}.base .top-container .left-info{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:50%;float:left;height:100%}.base .top-container .left-info .form{font-size:20px;vertical-align:middle;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;color:#fff;-webkit-align-items:flex-start;align-items:flex-start;margin-left:20px}.base .right-info{margin-top:30px;vertical-align:middle;background-color:#3498db;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:50%;height:35px}.base .right-info .form{vertical-align:middle;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;color:#fff;-webkit-align-items:flex-start;align-items:flex-start;margin-left:20px}.base .right-info .btn{vertical-align:middle;margin:0 0 10px 10px;font-size:20px;padding:10px 30px;border:0;background-color:#2386c8;color:#fff;border-radius:5px;border-color:#fff;transition:all 250ms ease-in-out;cursor:pointer}.base .right-info .btn:hover{background-color:#fff;color:#2386c8}.base .right-info .btn:focus{outline:none}.base .bottom-nav{width:100%;background-color:#3498db;color:#fff;text-align:center;margin-bottom:-20px}.base .book-data{-webkit-align-items:center;align-items:center;font-size:20px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;vertical-align:middle;width:100%;height:auto}.base .book-data .btn{-webkit-align-self:center;align-self:center;width:50%;margin:10px;background-color:#2386c8}

